* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
}

body {
  height: 100vh;
  background-color: #ff0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.calender {
  color: #f0f8ff;
  width: 450px;
  height: 520px;
  background-color: #000;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 4px 4px 8px #0006;
}

.month {
  width: 100%;
  height: 120px;
  text-align: center;
  background-color: #6a5acd;
  justify-content: center;
  align-items: center;
  display: flex;
}

.month h1 {
  text-transform: uppercase;
  letter-spacing: 3px;
  font-size: 35px;
  font-weight: 500;
}

.month p {
  font-size: 15px;
}

.weekdays {
  width: 100%;
  height: 50px;
  color: #6a5acd;
  background-color: #f0f8ff;
  display: flex;
}

.weekdays div {
  width: 100%;
  letter-spacing: 1px;
  justify-content: center;
  align-items: center;
  font-size: 15px;
  font-weight: 600;
  display: flex;
}

.dates {
  width: 100%;
  flex-wrap: wrap;
  padding: 2px;
  display: flex;
}

.dates div {
  width: 57.5px;
  height: 50px;
  cursor: pointer;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  margin: 3px;
  font-size: 14px;
  display: flex;
}

.dates div:hover:not(.empty) {
  border: 2px solid #f0f8ff;
}

.today {
  background-color: #6a5acd;
}

