* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Poppins", sans-serif;
}

body {
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-color: yellow;
}

.calender {
    background-color: black;
    color: aliceblue;
    width: 450px;
    height: 520px;
    border-radius: 10px;
    box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.4);
    overflow: hidden;
}

.month {
    width: 100%;
    height: 120px;
    background-color: slateblue;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.month h1 {
    font-size: 35px;
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: 3px;
}

.month p {
    font-size: 15px;
}

.weekdays {
    display: flex;
    width: 100%;
    height: 50px;
    background-color: aliceblue;
    color: slateblue;
}

.weekdays div {
    width: 100%;
    font-size: 15px;
    font-weight: 600;
    letter-spacing: 1px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.dates {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    padding: 2px;
}

.dates div {
    font-size: 14px;
    margin: 3px;
    width: 57.5px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    border-radius: 5px;
}

.dates div:hover:not(.empty) {
    border: 2px solid aliceblue;
}

.today {
    background-color: slateblue;
}